.coffees-page {
  display: flex;
  margin: 0 auto;
  max-width: 100%;
}

.region-search-input {
  width: 200px;
  height: 1.5rem;
  margin-bottom: 0.2rem;
  border: 1px solid rgb(175, 175, 175);
}

.no-results {
  padding: 5rem;
  font-size: 1.5rem;
}

@media only screen and (max-width: 600px) {
  .coffees-page {
    display: flex;
    flex-direction: column;
    max-width: 100%;
  }
}
