@import "../../variables";

.editor-coffee-card {
  display: flex;
  flex-direction: row;
  width: 325px;
  height: 400px;
  border-radius: 2rem;
  position: relative;
  justify-content: space-between;
  align-items: center;
  padding-top: 1em;
  padding-bottom: 1em;
}

.imgover {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: rgba(0, 0, 0, 0.5);
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 0.7) 25%,
    rgba(0, 0, 0, 0.8) 50%,
    rgba(0, 0, 0, 9) 100%
  );
  transition: opacity 0.25s;
  border-radius: 2rem;
}

.imgover:hover {
  opacity: 1;
}

.img-gradient {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 2rem;
}

.img-gradient:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: inline-block;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.3) 50%,
    rgba(0, 0, 0, 0.5) 100%
  );
  border-radius: 2rem;
}

.editor-coffee-image {
  object-fit: fill;
  width: 100%;
  height: 100%;
  border-radius: 2rem;
  z-index: -1;
}

.editor-details-section {
  position: absolute;
  color: white;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 20;
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  padding: 1em 1em;
}

.coffee-details-brand {
  font-family: $primaryFontFamily;
  font-size: 2.5rem;
  font-weight: 300;
  margin: 0;
}

.coffee-details-name {
  font-family: $primaryFontFamily;
  font-size: 1.5rem;
  font-weight: 200;
}

.coffee-details-paragraph {
  font-size: 1rem;
  font-weight: 200;
  width: 80%;
}

.coffee-details-chosen {
  margin: 0;
  margin-left: 1rem;
  font-weight: 100;
  font-size: 1.5rem;
}
.coffee-details-person {
  font-size: 1.5rem;
  font-weight: 300;
  margin: 0rem;
  margin-left: 2rem;
}

.editor-pic-container {
  position: absolute;
  right: 1rem;
  bottom: 1rem;
  margin-right: -4em;
}

.editor-pic {
  border-radius: 50%;
  height: 10em;
}

@media only screen and (max-width: 600px) {
  .editor-coffee-card {
    display: flex;
    flex-direction: row;
    width: 300px;
    height: 400px;
    border-radius: 2rem;
    position: relative;
    justify-content: space-between;
    align-items: center;
    padding-top: 1em;
    padding-bottom: 1em;
  }
}
